<script setup>
/* eslint-disable */
const props = defineProps(['text', 'type', 'jump', 'showif', 'index', 'color','avatar']);
defineEmits(['jumpto']);
</script>

<template>
<div style="position: relative;">
    <el-text class="Left" v-if="props.type == 'Left'"
    :style="{ color: props.color || 'white', textShadow: props.color ? `0 0 5px ${props.color}, 0 0 20px ${props.color}` : '0 0 10px white, 0 0 20px white' }">
    
    <p><el-avatar :size=40 class="avatar-left" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>{{ props.text }}</p>
  </el-text>
  <el-text class="Right" v-if="props.type == 'Right'"
    :style="{ color: props.color || 'white', textShadow: props.color ? `0 0 5px ${props.color}, 0 0 20px ${props.color}` : '0 0 10px white, 0 0 20px white' }">
    <p>{{ props.text }}<el-avatar :size=40 class="avatar-right" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar></p>
  </el-text>
  <el-text class="Center " v-if="props.type == 'Center'"><div class="centerdiv"><p>{{ props.text }}</p></div></el-text>
  <div class="Choice" v-if="props.type == 'Choice'">
    <el-text class="Choices" v-for="(i, ind) in props.text" :key="i">
      <p @click="$emit('jumpto', props.jump[ind], props.index)">{{ i }}</p>
    </el-text>
  </div>
  <div class="Left" v-if="props.type=='Image Left'">
    <el-avatar :size=40 class="avatar-left" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>
    <img class="image" :src="props.text" alt="Image Left"/>
  </div>
  <div class="Right" v-if="props.type=='Image Right'">
    <img class="image" :src="props.text" alt="Image Right"/>
    <el-avatar :size=40 class="avatar-right" v-if="props.avatar!=null">{{ props.avatar }}</el-avatar>
  </div>
</div>
</template>

<style scoped>
.avatar-left{
  text-shadow: none;
  position: relative;
  background-color: rgba(27, 27, 27,0);
  left: -2vh;
  top: -1vh;
}
.avatar-right{
  text-shadow: none;
  position: relative;
  background-color: rgba(27, 27, 27,0);
  left: 2vh;
  top: -1vh;
}
.image{
  position: relative;
  height: 20vh;
  width: 20*9/16vh;
  background-color: antiquewhite;
}
.div{
  position: relative;
}
.Choice {
  position: relative;
  bottom: 1vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 15px;
}

.Choices>p {
  margin-top: -2px;
  transition: transform 0.5s ease, color 0.5s ease, text-shadow 0.5s ease;
}

.Choices>p:hover {
  transform: scale(1.1);
  color: white;
  text-shadow: 0 0 10px white, 0 0 20px white;
}

p::selection {
  color: aqua;
  text-shadow: 0 0 10px aqua, 0 0 20px aqua;
}

.Left {
  position: relative;
  left: 10px;
  text-align: left;
  line-height: 50px;
}

.Right {
  position: relative;
  right: 10px;
  text-align: right;
  line-height: 50px;
}
</style>
