<script setup>
/* eslint-disable */
import { ref, nextTick, onMounted } from 'vue';
import Swal from 'sweetalert2';
import Dialogue from './Dialogue.vue';
import AudioCard from './AudioCard.vue';
import Title from './Title.vue';
const props = defineProps(['gameStatus', 'path']);
const list = ref([]);
const index = ref(-1);
const dialogueContainer = ref(null);
let story = {}
let maxNum = 0;
let cate = 0;
const audioSrc = ref("");
const bg = ref('');
const choosing = ref(false);
const T = ref("");
onMounted(() => {
  console.log(props.path);
  story = props.path;
  T.value = Object.keys(story)[cate++];
  console.log(T.value);
  maxNum = story[T.value].length;
})
function NextDialogue() {

  if (index.value != -1 && story[T.value][index.value]["goto"] == -1) {
    T.value = Object.keys(story)[cate++];
    console.log(T.value);
    maxNum = story[T.value].length;
    index.value = -1;
  }
  if (index.value >= 1 && story[T.value][index.value]["goto"] == "End") {
    Swal.fire({
      title: "Ending!!!",
      width: 600,
      padding: "3em",
      color: "#716add",
      background: "#fff ",
      backdrop: `
    rgba(0,0,123,0.4)
    left top
    no-repeat
  `
    });
    return;
  }
  if (index.value != -1) index.value = story[T.value][index.value]["goto"];
  else index.value++;
  list.value.push(story[T.value][index.value]);

  if (story[T.value][index.value].type == 'Choice') {
    choosing.value = true;
  }
  if (story[T.value][index.value].event != null) {
    const e = story[T.value][index.value].event;
    console.log(e);
    e.forEach(element => { EventHandler(element.e, element.val) })
  }
  nextTick(() => {
    if (dialogueContainer.value) {
      dialogueContainer.value.scrollTop = dialogueContainer.value.scrollHeight;
    }
  });
}

function JumpToNextDialogue(value, index_) {
  index.value = value;
  choosing.value = false;
  console.log(list.value);
  console.log(index_);
  list.value.pop();
  list.value.push(story[T.value][value]);
  if (index.value != -1 && story[T.value][index.value]["goto"] == -1) {
    T.value = Object.keys(story)[cate++];
    console.log(T.value);
    index.value = -1;
    return;
  }
  if (index.value >= 1 && story[T.value][index.value]["goto"] == "End") {
    Swal.fire({
      title: "Ending!!!",
      width: 600,
      padding: "3em",
      color: "#716add",
      background: "#fff ",
      backdrop: `
    rgba(0,0,123,0.4)
    left top
    no-repeat
  `
    });
    return;
  }
  if (story[T.value][index.value].type == 'Choice') {
    choosing.value = true;
  }
  if (story[T.value][index.value].event != null) {
    const e = story[T.value][index.value].event;
    console.log(e);
    e.forEach(element => { EventHandler(element.e, element.val) })
  }
  nextTick(() => {
    if (dialogueContainer.value) {
      dialogueContainer.value.scrollTop = dialogueContainer.value.scrollHeight;
    }
  });
  console.log(list.value);
  console.log(value);
}

function EventHandler(e, val) {
  if (e == "AudioPlay") {
    audioSrc.value = val;
  } else if (e == "ChangeBG") {
    bg.value = val;
  }
}
</script>

<template>
  <div>
    <div>
      <div :style="{ backgroundImage: bg ? `url(${bg})` : 'none', backgroundColor: bg ? 'transparent' : 'black' }"
        class="background-image-layer"></div> <!-- 背景层 -->
      <div class="background">
        <el-row class="gradient-row">
          <el-col :span="5" class="other-col"></el-col>
          <el-col :span="1" class="side-col-left"></el-col>
          <el-col :span="12" class="center-col">
            <Title :title="T"></Title>
            <div ref="dialogueContainer" class="dialogue-container">
              <TransitionGroup name="list">
                <div v-for="(i, index) in list" :key="i">
                  <Dialogue :type="i.type" :text="i.text" :jump="i.jump" @jumpto="JumpToNextDialogue" :index="index"
                    :color="i.color" :avatar="i.avatar" />
                </div>
              </TransitionGroup>
            </div>
            <button class="goon" v-if="choosing == false" @click="NextDialogue">Next</button>
          </el-col>
          <el-col :span="1" class="side-col-right"></el-col>
          <el-col :span="5" class="other-col">
            <AudioCard :source=audioSrc></AudioCard>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<style scoped>
* {
  overflow: hidden;
}

.background-image-layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.background {
  position: relative;
  z-index: 1;
  /* Ensures main content appears above the background image */
  background-color: rgba(0, 0, 0, 0.8);
  /* Optional overlay for better contrast */
  text-align: center;
  color: #fff;
}

.el-row {
  height: 100vh;
}

.side-col-left {
  background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(27, 27, 27, 0.8), rgba(27, 27, 27, 0.8));
}

.side-col-right {
  background: linear-gradient(to left, rgba(0, 0, 0, 1), rgba(27, 27, 27, 0.8), rgba(27, 27, 27, 0.8));
}

.center-col {
  background-color: rgba(27, 27, 27, 0.8);
  text-align: center;
  position: relative;
}

.dialogue-container {
  overflow-y: scroll;
  height: 85vh;
  /* Hide scrollbars for different browsers */
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.dialogue-container::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

button {
  border-radius: 50%;
  height: 100px;
  width: 100px;
  border: 3px solid;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 14px;
  padding: 10px 20px;
}

.goon {
  top: 87vh;
  left: 90vh;
  font-weight: 700;
}

.goon:hover {
  background-color: rgba(147, 147, 147, 0.9);
  box-shadow: 10px 10px 99px 6px rgb(162, 165, 166);
  transition: linear infinite 0.5s;
}


.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}

.list-enter-from,
.list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
</style>
